// Find all our documentation at https://docs.near.org
import { NearBindgen, near, call, view, initialize } from 'near-sdk-js';

interface TaskType {
  username: string;
  userAddress: string;
  taskTitle: string;
  taskDescription: string;
}

@NearBindgen({ requireInit: true})
class UserContract {

  @initialize({privateFunction: true})
  init({}) {
    this.tasks = []
    this.acceptedTasks = []
  }

  tasks : TaskType[] = [];
  acceptedTasks: TaskType[] = [];

  @call({})
  use_service({username, taskTitle}: {username: string, taskTitle: string}) {
    this.acceptedTasks.push(this.tasks.filter(task => 
      task.username == username && task.taskTitle == taskTitle)[0])
  }

  @call({})
  complete_service({username, taskTitle}: {username: string, taskTitle: string}) {
    this.acceptedTasks = this.acceptedTasks.filter(task => 
      task.username !== username && task.taskTitle !== taskTitle)
  }

  @call({})
  add_service({username, userAddress, taskTitle, taskDescription}: TaskType) {
      let jsonString = `{
        "username": "${username}",
        "userAddress": "${userAddress}",
        "taskTitle": "${taskTitle}",
        "taskDescription: "${taskDescription}"
      }`

      const newTask: TaskType = {
        username: username,
        userAddress: userAddress,
        taskTitle: taskTitle,
        taskDescription: taskDescription
      };
      this.tasks.push(newTask);
    }

  @view({})
  get_services() : TaskType[] {
      return this.tasks
    }
}